"use client";

import HeaderModern from "../components/layout/HeaderModern";
import Footer from "../components/layout/Footer";
import PageHeader from "../components/shared/PageHeader";
import GlassCard from "../components/ui/GlassCard";
import { Building2, Search, MapPin, Phone, Star, ArrowRight } from "lucide-react";
import { SHOWROOMS, CITIES } from "@/lib/data";
import Image from "next/image";
import Link from "next/link";
import { motion } from "framer-motion";

export default function ConcessionnairesPage() {
    return (
        <div className="min-h-screen bg-[#F8F9FA]">
            <HeaderModern />

            <PageHeader
                icon={Building2}
                badge="Réseau Officiel"
                title="Nos"
                highlight="Concessionnaires"
                description="Découvrez les meilleurs showrooms et concessionnaires agréés à travers l'Algérie."
            />

            <section className="py-12 max-w-7xl mx-auto px-6">
                {/* Search & Filter Bar */}
                <div className="bg-white rounded-2xl shadow-lg border border-gray-100 p-4 mb-12 -mt-20 relative z-20">
                    <div className="flex flex-col md:flex-row gap-4">
                        <div className="flex-1 relative">
                            <Search className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                            <input
                                type="text"
                                placeholder="Rechercher un concessionnaire..."
                                className="w-full pl-12 pr-4 py-3 bg-gray-50 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-[#FF6B35]/20 focus:border-[#FF6B35] transition-all"
                            />
                        </div>
                        <select className="px-4 py-3 bg-gray-50 border border-gray-200 rounded-xl md:w-64 focus:outline-none focus:ring-2 focus:ring-[#FF6B35]/20 focus:border-[#FF6B35]">
                            <option value="">Toutes les villes</option>
                            {CITIES.map(city => <option key={city} value={city}>{city}</option>)}
                        </select>
                        <button className="px-8 py-3 bg-[#FF6B35] hover:bg-[#E85D2E] text-white rounded-xl font-bold transition-all shadow-lg shadow-[#FF6B35]/20">
                            Rechercher
                        </button>
                    </div>
                </div>

                {/* Grid */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {SHOWROOMS.map((showroom, index) => (
                        <motion.div
                            key={showroom.id}
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            transition={{ delay: index * 0.1 }}
                        >
                            <GlassCard className="h-full hover:border-[#FF6B35]/30">
                                <div className="relative h-56">
                                    <Image src={showroom.image} alt={showroom.name} fill className="object-cover" />
                                    <div className="absolute inset-0 bg-gradient-to-t from-black/70 to-transparent" />

                                    <div className="absolute bottom-4 left-4 text-white">
                                        <h3 className="text-xl font-bold mb-1">{showroom.name}</h3>
                                        <div className="flex items-center gap-1 text-sm opacity-90">
                                            <MapPin className="w-3 h-3 text-[#FF6B35]" /> {showroom.city}
                                        </div>
                                    </div>

                                    {showroom.rating && (
                                        <div className="absolute top-4 right-4 bg-white/20 backdrop-blur-md px-2 py-1 rounded-lg flex items-center gap-1 text-white text-sm font-bold border border-white/30">
                                            <Star className="w-3 h-3 fill-yellow-400 text-yellow-400" /> {showroom.rating}
                                        </div>
                                    )}
                                </div>

                                <div className="p-6">
                                    <div className="space-y-4 mb-6">
                                        <div className="flex items-start gap-3">
                                            <div className="w-8 h-8 rounded-full bg-[#FF6B35]/10 flex items-center justify-center flex-shrink-0">
                                                <MapPin className="w-4 h-4 text-[#FF6B35]" />
                                            </div>
                                            <p className="text-sm text-gray-600 leading-relaxed">{showroom.address}</p>
                                        </div>
                                        <div className="flex items-center gap-3">
                                            <div className="w-8 h-8 rounded-full bg-[#FF6B35]/10 flex items-center justify-center flex-shrink-0">
                                                <Phone className="w-4 h-4 text-[#FF6B35]" />
                                            </div>
                                            <p className="text-sm font-medium text-gray-900">{showroom.phone}</p>
                                        </div>
                                    </div>

                                    <div className="flex items-center justify-between pt-4 border-t border-gray-100">
                                        <span className="px-3 py-1 bg-gray-100 text-gray-600 text-xs font-bold uppercase rounded-full">
                                            {showroom.cars} Véhicules
                                        </span>
                                        <Link
                                            href={`/concessionnaires/${showroom.id}`}
                                            className="flex items-center gap-1 text-[#FF6B35] font-bold text-sm hover:gap-2 transition-all"
                                        >
                                            Voir le stock <ArrowRight className="w-4 h-4" />
                                        </Link>
                                    </div>
                                </div>
                            </GlassCard>
                        </motion.div>
                    ))}
                </div>
            </section>

            <Footer />
        </div>
    );
}
